.container{
    height: 100vh;
    width: 100%;
    position: relative;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(12, 1fr);
}

.tools-text{
    color: var(--color-tertiary);
    margin-top: 1rem;
    margin-right: 1rem;
    text-align: right;

    grid-column: 4 / 13;
    grid-row: 2/4;

}

.tools-text h2{
    font-size: 1.5rem;
    margin-bottom: 1rem;
}
.tools-text p{
    font-size: 1.2rem;
}

.tools-img{
    position: relative;
    width: 80vw;
    overflow-x:scroll;
    overflow-y: hidden;
    grid-column: 4 / 13;
    grid-row: 5/9;
    display: flex;
    gap: 10px;
    margin: 0 auto 1rem;
    padding: 0 100px;

     -webkit-mask-image: radial-gradient(circle, rgba(31,31,31,1) 51%, rgba(0,0,0,0) 100%);
    --mask-image: linear-gradient(to right, black, transparent);
   -webkit-mask-size: 100%; 
    -webkit-mask-position: center; 

}

.tools-img::-webkit-scrollbar{
    position: absolute;
    background-color: var(--color-background);
    height: 10px;
}
.tools-img::-webkit-scrollbar-thumb{
    border-radius: 10px;
    background-color: var(--color-tertiary);
    background-clip: content-box;
    width: 1px;
}
.tools-img svg{
    width: clamp(40px, 6rem, 25vw);
    margin: auto;
}

.tools-img .tool{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    padding: 15px;
}
.tools-img .tool span{
    color: var(--color-tertiary);
    font-weight: bold;
}

.tools-img svg:hover{
    cursor: auto;
    filter: none;
}



